@charset "UTF-8";
@font-face {
  font-family: Despairs;
  src: url("../assets/fonts/despairs.ttf") format("truetype");
}
:root {
  --font-brand: Despairs;
  --font-body: Arial, sans-serif;
  --transition-min: .3s ease-in-out;
  --transition-max: 1.2s ease-in;
}

:root {
  --white: hsl(0, 0%, 100%);
  --gray-400: hsl(210, 10%, 70%);
  --gray-700: hsl(0, 0%, 16%);
  --gray-800: hsl(0, 0%, 14%);
  --black: hsl(0, 0%, 0%);
  --brand: hsl(45, 100%, 50%);
}

html {
  box-sizing: border-box !important;
  scroll-behavior: smooth;
  overflow: hidden;
  font-size: 100%;
}
html:has(.loader--hidden) {
  overflow: visible;
}

*,
*::before,
*::after {
  box-sizing: inherit !important;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
}
body canvas {
  position: absolute;
  top: 9.375rem;
  transform: translateX(-5%);
}
body .overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.8);
  transition: opacity 200ms;
  visibility: hidden;
  opacity: 0;
  z-index: 10;
}
body .overlay .cancel {
  position: absolute;
  width: 100%;
  height: 100%;
  cursor: default;
}
body .overlay:target {
  visibility: visible;
  opacity: 1;
}
body .popup {
  padding: 2.5rem;
  margin: 6.25rem auto;
  background-color: var(--white);
  max-width: 49.375rem;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 1.25rem;
}
body .popup__inner {
  margin: 0 1.25rem;
}
body .popup .close {
  position: absolute;
  width: 1.25rem;
  top: 0.625rem;
  right: 0.9375rem;
  transition: all 200ms;
  text-align: center;
  font-size: 1.875rem;
  font-weight: bold;
  text-decoration: none;
  color: var(--gray-400);
}
body .popup input[type=radio],
body .popup .btn {
  margin-top: 1.25rem;
}
body .wave {
  overflow: hidden;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 6.25rem;
}
body .wave__inner {
  position: absolute;
  width: 200%;
  height: 100%;
  transform-origin: 0 100%;
  background: url(../images/content/wave.png) 0/50% 6.25rem;
  animation: wave 15s linear infinite;
}
@keyframes wave {
  0% {
    transform: translateX(0) scaleY(1);
  }
  50% {
    transform: translateX(-25%) scaleY(0.55);
  }
  100% {
    transform: translateX(-50%) scaleY(1);
  }
}
body .loader {
  position: fixed;
  z-index: 50;
  width: 100%;
  height: 100%;
  background-color: var(--gray-700);
}
body .loader--hidden {
  opacity: 0;
  visibility: hidden;
}
body .loader svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg) scale(1);
}
body .loader .line {
  stroke: var(--gray-800);
}
body .loader .line__animation {
  animation: loading var(--transition-max) infinite;
}
@keyframes loading {
  0% {
    stroke: var(--brand);
    stroke-dasharray: 0 200;
  }
  50% {
    stroke-dashoffset: 0;
    stroke-dasharray: 200 200;
  }
  100% {
    stroke: var(--brand);
    stroke-dashoffset: -200;
    stroke-dasharray: 200 200;
  }
}
body footer {
  text-align: center;
  color: var(--gray-400);
  padding: 20px 0;
}

h1,
h2,
h3 {
  margin: 0 0 2.5rem;
  line-height: 1.1;
  position: relative;
  text-transform: uppercase;
}

h1 {
  font-size: 4.125rem;
  text-transform: uppercase;
}

h2 {
  font-size: 2.5rem;
}

h3 {
  font-size: 1.25rem;
}

.subtitle {
  font-size: 1.5rem;
  opacity: 0.6;
}

p {
  font-size: 1.125rem;
  opacity: 0.6;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: var(--black);
}

input:not([type=radio]),
textarea {
  border: 0.125rem solid var(--gray-400);
  padding: 0.75rem 5rem 0.75rem 1.25rem;
  box-shadow: -0.375rem 0 0 var(--gray-400) inset;
}

textarea {
  resize: vertical;
  font-family: var(--font-body);
}

.hidden {
  display: none;
}

.btn,
input[type=submit] {
  font-size: 1.125rem;
  font-weight: 700;
  display: inline-flex;
  align-items: center;
  overflow: hidden;
  color: var(--brand);
  background-color: transparent;
  border: 0.125rem solid var(--brand);
  transition: var(--transition-min);
  padding: 0.3125rem 1.25rem;
  border-radius: 0.625rem;
  box-shadow: none;
  cursor: pointer;
}
.btn img,
input[type=submit] img {
  position: relative;
  transition: var(--transition-min);
  left: 4.375rem;
}
.btn:hover,
input[type=submit]:hover {
  color: var(--black);
  background-color: var(--brand);
}
.btn:hover img,
input[type=submit]:hover img {
  left: 0.5rem;
}

.btn__job {
  position: absolute;
  right: 1.25rem;
  bottom: -1.125rem;
}
.btn--del {
  color: var(--brand);
  border-color: var(--black);
  background-color: var(--black);
}

.logo {
  padding: 1.5625rem;
  font-weight: 700;
  font-size: 1.875rem;
  font-family: var(--font-brand);
  text-transform: uppercase;
  position: fixed;
  z-index: 3;
  color: var(--white);
  background-color: var(--black);
}
.logo::after {
  content: "";
  position: absolute;
  top: 4.375rem;
  left: 0;
  width: 100%;
  height: 2.5rem;
  background-color: var(--black);
  transition: var(--transition-min);
  clip-path: polygon(50% 50%, 100% 100%, 100% 0, 0 0, 0 100%);
}
.logo:hover::after {
  top: 5rem;
}

span:not(.error) {
  color: var(--brand);
}

.container {
  padding: 0 1.5625rem;
  max-width: 75rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container:not(.header__inner) {
    padding: 3.75rem 0;
  }
}

.header {
  background-color: var(--white);
}
.header .menu--active {
  box-shadow: 0 0.625rem 0 var(--brand) inset;
}
.header .menu ul {
  display: flex;
  align-items: center;
  justify-content: right;
}
.header .menu ul li + li {
  margin-left: 1.25rem;
}
.header .menu ul a:not(.btn, .btn__search) {
  display: block;
  padding: 1.875rem 0;
  transition: var(--transition-min);
}
.header .menu ul a:not(.btn, .btn__search):hover {
  box-shadow: 0 0.625rem 0 var(--brand) inset;
}

.hero {
  min-height: 54.6875rem;
  font-size: 2.5rem;
  background-color: var(--black);
  color: var(--white);
  margin-bottom: 12.5rem;
  position: relative;
}
.hero__inner {
  max-width: 31.25rem;
  transform: translateY(50%);
}

.job {
  position: unset;
  display: flex;
  flex-direction: column-reverse;
}
.job__slider {
  position: relative;
}
.job__item {
  display: block;
  padding: 1.25rem;
  border-radius: 1.25rem;
  border: 0.0625rem solid var(--gray-400);
  margin-bottom: 2.5rem;
  word-wrap: break-word;
  position: relative;
  overflow: hidden;
}
.job__item div:last-child {
  display: flex;
  align-items: center;
}
.job__item div:last-child * + * {
  margin-left: 1.25rem;
}
.job__item .demand {
  float: right;
  opacity: 0.5;
}
.job__subtitle {
  font-size: 0.875rem;
}
.job__image {
  filter: grayscale(1);
  width: 3.125rem;
  margin: 0 auto;
  display: block;
  transition: var(--transition-min);
}
.job__image--wide {
  transform: scale(2);
}
.job .swiper-slide {
  margin-bottom: 0;
}
.job .swiper-slide:hover img {
  filter: none;
}
.job .swiper-slide p {
  margin-bottom: 0;
  text-align: center;
}
.job .swiper-button-prev,
.job .swiper-button-next {
  border-radius: 5px;
  background-color: var(--black);
  color: var(--white);
  transform: scale(1.3);
}
.job .swiper-button-prev::after,
.job .swiper-button-next::after {
  transform: scale(0.3);
}
.job .swiper-button-prev {
  left: -15px;
}
.job .swiper-button-next {
  right: -15px;
}

.description {
  display: flex;
  align-items: center;
  text-align: right;
}
.description > * {
  width: 50%;
}
.description .worker {
  width: 15rem;
  height: 20rem;
}
.description .worker *:not(.swiper-slide) {
  background-color: transparent !important;
}
.description .worker__item {
  border-radius: 1.25rem;
  font-size: 1.25rem;
  font-weight: bold;
  text-align: center;
  color: var(--white);
  position: relative;
}
.description .worker__item::after {
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 100%;
  height: 4.6875rem;
  padding: 0.625rem;
  transform: translateX(-50%);
  background-color: var(--black);
}
.description .worker__item:nth-child(1n) {
  background-color: rgba(206, 17, 17, 0.8);
}
.description .worker__item:nth-child(1n)::after {
  content: "Дизайнер";
}
.description .worker__item:nth-child(2n) {
  background-color: rgba(0, 140, 255, 0.8) !important;
}
.description .worker__item:nth-child(2n)::after {
  content: "Разработчик" !important;
}
.description .worker__item:nth-child(3n) {
  background-color: rgba(10, 184, 111, 0.8) !important;
}
.description .worker__item:nth-child(3n)::after {
  content: "Бухгалтер" !important;
}
.description .worker__item > img {
  width: 15.625rem;
  height: 20rem;
}
.description .worker__stars {
  position: relative;
  bottom: 2.375rem;
  z-index: 1;
}
.description .worker__stars img {
  width: 1.25rem;
}

section:has(.feedback) h2 {
  text-align: center;
}

.feedback {
  display: block;
  position: relative;
  padding: 0 !important;
  margin: 0 auto 1.5625rem;
  border: 3.125rem solid var(--black);
}
.feedback__inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.feedback form {
  font-size: 1.25rem;
}
.feedback form > *:not(:last-child) {
  width: 100%;
  margin-bottom: 2.5rem;
  border-width: 0;
  box-shadow: none;
  padding: 1.5625rem;
  background-color: rgba(0, 0, 0, 0.7);
}
.feedback form > *:not(:last-child), .feedback form > *:not(:last-child)::placeholder {
  color: var(--white);
  font-size: 1rem;
}
.feedback form button {
  background-color: var(--black);
  color: var(--white);
  padding: 1.25rem;
  width: 50%;
  border: none;
  position: relative;
  top: 5.875rem;
  left: 50%;
  transform: translateX(-50%);
  font-size: 1.5625rem;
  font-weight: 700;
  cursor: pointer;
}

.data {
  margin: 3.125rem 0;
  width: 100%;
}
.data:has(table) {
  overflow-x: scroll;
  box-shadow: 0.0625rem 0 0 var(--gray-400) inset, -0.0625rem 0 0 var(--gray-400) inset;
}
.data__table {
  border-collapse: collapse;
  border-color: var(--gray-400);
  width: 100%;
}
.data__table th,
.data__table td {
  padding: 0.625rem;
}
.data__table th .btn,
.data__table td .btn {
  width: 100%;
  justify-content: center;
}
.data__table th .btn:not(:last-child),
.data__table td .btn:not(:last-child) {
  margin-bottom: 0.625rem;
}

.form {
  display: flex;
}
.form label {
  display: block;
  text-align: right;
}
.form label input,
.form label textarea {
  width: 21.875rem;
}

.portfolio {
  display: flex;
  flex-direction: column;
}
.portfolio__content {
  display: flex;
  justify-content: space-between;
  order: -1;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  gap: 1.875rem;
}
.gallery .img-wrapper {
  height: 25rem;
  overflow: hidden;
  transition: var(--transition-min);
  display: block;
}
.gallery .img-wrapper:hover {
  box-shadow: 0 0 3.125rem var(--black);
}
.gallery .img-wrapper img {
  border: 0.125rem solid var(--black);
  width: 23.75rem;
  height: 100%;
  transition: 1s;
  position: relative;
  object-fit: cover;
  object-position: top center;
}
.gallery .img-wrapper img:hover {
  object-position: bottom center;
}
.gallery .btn--del {
  border-radius: 0;
  position: relative;
  top: -0.125rem;
}

.error {
  padding: 0.625rem;
  border-radius: 1.5625rem;
  color: var(--white);
  font-weight: 700;
  background-color: var(--gray-400);
  position: relative;
  text-align: center;
  width: 26.5625rem;
}
.error::before {
  content: "";
  width: 1.5625rem;
  height: 1.5625rem;
  display: inline-block;
  background-color: var(--gray-400);
  clip-path: polygon(50% 0, 0 100%, 100% 100%);
  position: absolute;
  top: -1.5625rem;
  left: 1.5625rem;
}